.layout-container {
  position: relative;
  display: flex;
  background-color: #fff;
}

.layout-sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 3.5rem;
  width: 200px;
  height: calc(100vh - 3.5rem);
  background-color: #fff;
  border-right: 1px solid rgba(0, 0, 0, 0.125);
}

.layout-sidebar.show {
  transform: translateX(0%);
}

.layout-sidebar.show ~ .layout-sidebar-overlay {
  display: block;
}

@media (max-width: 767.98px) {
  .layout-sidebar {
    position: fixed;
    z-index: 1110;
    top: 3.5rem;
    bottom: 0;
    left: 0;
    transition: transform .2s;
    transform: translateX(-100%);
  }
}

@media (min-width: 768px) {
  .layout-sidebar {
    transform: translateX(0%);
  }
}

.layout-sidebar .nav-item:hover {
  background-color: #f2f2f2;
}

.layout-sidebar .nav-root > .nav-item.active {
  background-color: #ededed;
  box-shadow: inset 4px 0 0 #007bff;
}

.layout-sidebar .nav-item.active > .nav-link {
  color: #007bff;
  font-weight: 700;
}

.layout-sidebar .nav-link {
  display: flex;
  padding: 0.75rem;
  color: #495057;
}

.layout-sidebar .nav-link:hover {
  color: #212529;
}

.layout-sidebar .nav-link-icon {
  width: 1.25em;
  margin-right: 0.5rem;
}

.layout-sidebar .nav-link-name {
  flex: 1;
}

.layout-sidebar .submenu {
  padding: 0;
  margin: 0;
  list-style: none;
}

.layout-sidebar .submenu .nav-link {
  padding-left: 2.5rem;
}

.layout-sidebar .submenu .nav-item.active > .nav-link {
  background-color: rgba(0, 0, 0, 0.03);
}

.layout-sidebar-header {
  font-size: 1.25rem;
  font-weight: bold;
  padding: 0.75rem;
  margin: 0;
}

.layout-sidebar-content {
  height: 100%;
  flex-wrap: nowrap;
  overflow-y: auto;
}

.layout-sidebar-overlay {
  position: fixed;
  z-index: 1100;
  top: 3.5rem;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  background-color: rgba(0, 0, 0, 0.2);
}

.layout-content-wrapper {
  max-width: 100%;
  min-height: 100vh;
  padding-top: 3.5rem;
  flex: 1;
}

.layout-content {
  padding: 0;
}

.layout-alert, .layout-alert .alert {
  margin: 0;
  border-width: 0 0 1px;
  border-radius: 0;
}

.layout-breadcrumb {
  display: flex;
  align-items: center;
}

.layout-breadcrumb nav {
  flex: 1;
}

.layout-breadcrumb .breadcrumb {
  background-color: transparent;
  margin: 0;
  border-radius: 0;
  white-space: nowrap;
}

@media (max-width: 767.98px) {
  .layout-breadcrumb .breadcrumb {
    padding: 0.75rem;
  }
}

.layout-sidebar-toggler {
  background-color: transparent;
  padding: 0.5rem;
  border: none;
}

.layout-sidebar-toggler-icon {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
  background: no-repeat center center;
  background-size: 100% 100%;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 12 16' xmlns='http://www.w3.org/2000/svg' version='1.1'%3E%3Cpath fill='%23000' fill-rule='evenodd' d='M11.41 9H.59C0 9 0 8.59 0 8c0-.59 0-1 .59-1H11.4c.59 0 .59.41.59 1 0 .59 0 1-.59 1h.01zm0-4H.59C0 5 0 4.59 0 4c0-.59 0-1 .59-1H11.4c.59 0 .59.41.59 1 0 .59 0 1-.59 1h.01zM.59 11H11.4c.59 0 .59.41.59 1 0 .59 0 1-.59 1H.59C0 13 0 12.59 0 12c0-.59 0-1 .59-1z'%3E%3C/path%3E%3C/svg%3E");
}

.layout-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .layout-sidebar-toggler,
  .layout-sidebar-overlay {
    display: none !important;
  }
}

body.show-sidebar {
  overflow: hidden;
}
